import React, { Component } from 'react';
import {Checkbox,Button} from 'react-bootstrap';
import {connect} from 'react-redux';
import {sublogin} from "../../api/login";

import './login.scss';

class Login extends Component {

    constructor(){
        super();
        this.subUserPws = this.subUserPws.bind(this);
        this.user = this.user.bind(this);
        this.password = this.password.bind(this);
        this.state = {
            user:'',
            password:''
        }
    }

    //登陆
    subUserPws(){
        if(!this.state.user) alert('请输入正确的用户名');
        if(!this.state.password) alert('请输入密码');
        let postData = {
            user:this.state.user,
            password:this.state.password
        }
        sublogin(postData).then(data=>{
            console.log(this.props.login);
            this.props.login(true);
            this.props.history.push("/");
        }).catch(err=>{
            alert(err)
        })
    }
    user(event){
        this.setState({user:event.target.value})
    }
    password(event){
        this.setState({password:event.target.value})
    }

    render() {
        return (
            <div className="Login content">
                <div className="login_m">
                    <div className="login_boder">
                        <div className="login_padding">
                            <div>
                                <h2>USERNAME</h2>
                                <div>
                                    <input type="text" value={this.state.user} onChange={this.user} className="txt_input"/>
                                </div>
                            </div>
                            <div>
                                <h2>PASSWORD</h2>
                                <div>
                                    <input type="password" value={this.state.password} onChange={this.password} className="txt_input"/>
                                </div>
                            </div>
                            <p>Forgot your password?</p>
                            <div className="sub_rem">
                                <Checkbox checked readOnly>
                                    Remember me
                                </Checkbox>
                                <Button bsStyle="info" bsSize="xsmall" onClick={this.subUserPws}>SIGN-IN</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return (
        state
    )
}

export default connect(
    mapStateToProps
)(Login)
